import React, { Component } from "react";

import "./index.scss";

import {connect} from "react-redux"

import Head from "&/components/head";
import  {UploadImg}  from "&/components/upload"
import { List } from "antd-mobile"
import { getUserMobile } from "../../actions";
const Item = List.Item;


const list = ['待付款', '查看物流', '待评价'];
const blist = ['礼品卡', '优惠卷', '积分']
const count=['0','3','100']
const icons = [
    require("@/assets/images/qb.png"),
    require("@/assets/images/wl.png"),
    require("@/assets/images/pj.png"),
]
const jpg=[
    require("@/assets/images/mine.png"),
    require("@/assets/images/download.png"),
    require("@/assets/images/mb.png"),
    require("@/assets/images/kf.png"),
]
const sort=['用户中心','积分商场','每日签到','联系客服']


@connect(
    state=>(
         {
            mobile:state.getIn(['data','mobile'])
        }
    )
)
 class Mine extends Component {
    // constructor() {
    //     super();
    //     this.state = {
    //         mobile: ''
    //     }
    // }

    componentDidMount() {
      

        const {
            mobile,
            dispatch
        } = this.props;
        if(!mobile){
            dispatch(getUserMobile({url:"/react/getMobile"}))
        }

    }


    render() {
        const {
            mobile,
        } = this.props
        return (
            <div className="mine">
                <Head title="个人中心" location={this.props.location}></Head>
                
                    <div className="mine">
                        <UploadImg mobile={mobile}></UploadImg>
                    </div>
                
                <List className="my-list">
                    <Item extra="全部订单" arrow="horizontal" onClick={() => { }}>我的订单</Item>
                </List>
                <div className="icon">
                    {
                        icons.map((icon, a) => {
                            return (
                                <img className="img" src={icon} key={a} alt="" />
                            )
                        })
                    }
                </div>
                <ul className="ul">
                    {
                        list.map((l, b) => {
                            return (
                                <li key={b}><a href=""
                                    
                                >{l}</a></li>
                            )
                        })
                    }
                </ul>
                <div className="mypack">我的钱包</div>
                <div className="count">
                    {
                        count.map((c, i) => {
                            return (
                            <span key={i}>{c}</span>
                            )
                        })
                    }
                </div>
                
                <ul className="bl">
                    {
                        blist.map((l, d) => {
                            return (
                                <li key={d}><a href=""
                                    
                                >{l}</a></li>
                            )
                        })
                    }
                </ul>

               <div className="reversTwo">
               <div className="jpg">
                {
                        jpg.map((icon, e) => {
                            return (
                               <div key={e}> <img className="jpgimg" src={icon}  alt="" /></div>
                            )
                        })
                    }
                </div>
                
                <ul className="sort">
                    {
                        sort.map((sort, f) => {
                            return (
                                <li key={f}><a href=""
                                    
                                >{sort}</a></li>
                            )
                        })
                    }
                </ul>
               </div>
               <div className="reversTwo">
               <div className="jpg">
                {
                        jpg.map((icon, g) => {
                            return (
                               <div key={g}> <img className="jpgimg" src={icon}  alt="" /></div>
                            )
                        })
                    }
                </div>
                
                <ul className="sort">
                    {
                        sort.map((s, h) => {
                            return (
                                <li key={h}><a href=""
                                    
                                >{s}</a></li>
                            )
                        })
                    }
                </ul>
               </div>
            </div>
        )
    }
}

export default Mine